import { useState } from 'react'
import { ChangeEventInput } from 'types'
import { inicialState } from 'utils/inicialStateForms'

const useForm = () => {
  const [state, setState] = useState(inicialState)

  const handleChange: ChangeEventInput = e => setState({ ...state, [e.target.id]: e.target.value })

  return {
    state,
    handleChange
  }
}

export default useForm