import useForm from 'hooks/useForm'

const FormLogin = () => {
  const { state, handleChange } = useForm()
  const { email, password } = state

  console.log(email, password)
  return <form>
    <div className="inputsContainer">
      <label htmlFor="email">Email</label>
      <input type="email" id="email" placeholder="correo@correo.com" value={email} onChange={handleChange} required/>
    </div>
    <div className="inputsContainer">
      <label htmlFor="password">Contraseña</label>
      <input type="password" id="password" placeholder="Ingrese su contraseña" value={password} onChange={handleChange} required/>
    </div>
    <button aria-label="Login">Iniciar sesión</button>
  </form>
}

export default FormLogin